<template>
<!-- https://www.kumaodm.com -->
        <div>
          <swipe class="my-swipe">
            <swipe-item class="slide1"><h1 style="font-size:80px"></h1></swipe-item>
            <swipe-item class="slide2"></swipe-item>
            <swipe-item class="slide3"></swipe-item>
            <swipe-item class="slide4"></swipe-item>
            <swipe-item class="slide5"></swipe-item>
          </swipe>
          <div class="nav">
            <p>我的书架</p>
            <ul class="pic">
            <li v-for="music in bookList" :key="music.id">
                   <img :src="music.src" />
           </li>
              
            </ul>
          </div>
        </div>
        
        
</template>

<script>

import Axios from "axios";

import CommonHeader from '../common/Header'
import CommonFooter from '../common/Footer'
import { Swipe, SwipeItem } from 'vue-swipe';
import 'vue-swipe/dist/vue-swipe.css';

export default {
    components:{
        CommonHeader,
        CommonFooter,
        'swipe': Swipe,
        'swipe-item': SwipeItem
    },
    data(){
        return {
            bookList: []
        }
    },
   mounted() {
       let url = "./static/data/booklist.json"

       Axios.get(url).then(res => {
           this.bookList = res.data.books;
           console.log(res, this.bookList);
       }).catch(); 
    }
}
</script>

<style scoped>
.list{ margin:0.2rem 0; display:flex; flex-wrap: wrap; }
.list li{width:50%;}
.list li img{width:100%;}
.nav{text-align: center;}
.nav p{font-size: 20px;font-weight: bold;}
.pic{display: flex; flex-wrap:  wrap;justify-content:center;}
.nav .pic li{height: 130px; width: 30%;;margin: 1%;background: #ccc;text-align: center;}


.my-swipe {
  height: 150px;
  color: #fff;
  font-size: 30px;
  margin-top: -15px;
  text-align: center;
}

.slide1 {
  background-image: url('https://img3.doubanio.com/lpic/s27102925.jpg');
  color: #fff;
}

.slide2 {
  background-image: url('https://img3.doubanio.com/lpic/s6989253.jpg');
  color: #000;
}

.slide3 {
  background-image: url('/static/img/a.jpg');
  color: #fff;
  background-size: 100%;
}
.slide4 {
  background-image: url('/static/img/b.jpg');
  color: #fff;
  background-size: 100%;
}
.slide5 {
  background-image: url('/static/img/c.jpg');
  color: #fff;
  background-size: 100%;
}

</style>



