<template>
   <div class="albums">
    <ul class="tou">
        <li><router-link to="/music/musicAlbums">精选</router-link></li>
        <li><router-link to="/music/musicList">分类</router-link></li>
    </ul>
       <aplayer autoplay :list="songs" :showLrc="isLrc"
        :music="{
            title: 'secret base~好听吗!',
            artist: 'Silent Siren',
            src: 'https://moeplayer.b0.upaiyun.com/aplayer/secretbase.mp3',
            pic: 'http://singerimg.kugou.com/uploadpic/softhead/200/20180814/20180814162531430.jpg',
            lrc: 'https://api.bzqll.com/music/netease/lrc?key=579621905&id=526307800'
        }"
        />
   </div> 
</template>

<script>
import Axios from 'axios'
import Aplayer from 'vue-aplayer'

export default {
    components: {
        Aplayer
    },
    data(){
        return {
            songs: [],
            isLrc: false,
        }
    },
    mounted(){
        let url = './static/data/musicdata.json'
        let list = []

        Axios.get(url).then(res => {
            list = res.data.musicData;
            list.forEach(tune => {
                this.songs.push({
                    title: tune.title,
                    artist: tune.author,
                    src: tune.src,
                    pic: tune.musicImgSrc,
                    lrc: tune.lrc
                });
            });
            this.isLrc = true;
            console.log(this.songs);
        }).catch();
    },
    props: ['showLrc'],
  
}
</script>

<style scoped>
.albums{margin: 1.2rem 0;}
.tou{display:flex; justify-content: space-evenly;}
.tou li{width: 30%; font-size: 14px; text-align: center;background: #ccc;border-radius:20px; }
</style>



