<style scoped>
.list{ margin:0.2rem 0; display:flex; flex-wrap: wrap; }
.list li{width:50%;}
.list li img{width:100%;}
.tou{display:flex; justify-content: space-evenly;}
.tou li{width: 30%; font-size: 14px; text-align: center;background: #ccc;border-radius:20px; }
</style>

<template>
   <div>
    <ul class="tou">
        <li><router-link to="/music/musicAlbums">精选</router-link></li>
        <li><router-link to="/music/musicList">分类</router-link></li>
    </ul>
       <ul class="list">
           <li v-for="music in musicList" :key="music.id">
               <router-link :to="'/music/MusicAlbums/'">
                   <img :src="music.bg" />
               </router-link>           
           </li>
       </ul>
   </div> 
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            musicList: []
        }
    },
    mounted() {
       let url = "./static/data/musiclist.json"

       Axios.get(url).then(res => {
           this.musicList = res.data.albums;
           console.log(res, this.musicList);
       }).catch(); 
    }
}
</script>


