<template>
    <div>
        <common-header>
            <span slot="title">个人中心</span>
        </common-header>
           <!-- <input type="text" class="txt"><input type="button" value="取消" class="btn">
           <hr> -->
           <div class="page">
          <ul>
            <li><img src="static/img/bg10.jpg" alt=""><span>会员</span></li>
            <li><img src="static/img/bg9.jpg" alt=""><span>反馈</span></li>
            <li><img src="static/img/bg8.jpg" alt=""><span>缓存</span></li>
            <li><img src="static/img/bg7.jpg" alt=""><span>分享</span></li>
            <li><img src="static/img/bg6.jpg" alt=""><span>推荐</span></li>
            <li><img src="static/img/bg5.jpg" alt=""><span>书单</span></li>
            <li><img src="static/img/bg4.jpg" alt=""><span>历史</span></li>
            <li><img src="static/img/bg3.jpg" alt=""><span>提醒</span></li>
            <li><img src="static/img/bg2.jpg" alt=""><span>帖子</span></li>
            <li><img src="static/img/bg1.jpg" alt=""><span>私信</span></li>
           
            <img src="static/img/bg0.jpg" alt="" style="width:60px"><span>设置</span>
          </ul>
        </div>
        <common-footer></common-footer>
    </div>
</template>

<script>
import CommonHeader from '../common/Header'
import CommonFooter from '../common/Footer'

export default {
    components:{
        CommonHeader,
        CommonFooter,
    }
}
</script>

<style scoped>
.txt{width: 80%; height: 25px;border-radius: 10px;margin:0 2% 2% 2%;}
.btn{width: 12%;height: 25px;border-radius: 10px;}
.page{width: 70%;height: 100%;background: #fff; position: fixed;left: 0;top:50px;}
.page li {height: 40px; border-bottom:1px solid #ccc; }
.page li img{width: 40px;height: 40px;}
.page li span{height: 40px;margin-left: 10px;font-size: 20px;line-height: 40px;color: #000;}
.page  span{font-size: 30px;margin-left: 5px;}
</style>



