<template>
    <div>
        <swipe class="my-swipe">
            <swipe-item class="slide1"></swipe-item>
            <swipe-item class="slide2"></swipe-item>
            <swipe-item class="slide3"></swipe-item>
            <swipe-item class="slide4"></swipe-item>
            <swipe-item class="slide5"></swipe-item>
        </swipe>
    </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vue-swipe';
import 'vue-swipe/dist/vue-swipe.css';


export default {
    components: {
        'swipe': Swipe,
        'swipe-item': SwipeItem
    }
}
</script>

<style scoped>
.my-swipe {
  height: 150px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  background-image: url('https://img3.doubanio.com/lpic/s27102925.jpg');
  color: #fff;
}

.slide2 {
  background-image: url('https://img3.doubanio.com/lpic/s6989253.jpg');
  color: #000;
}

.slide3 {
  background-image: url('/static/img/a.jpg');
  color: #fff;
  background-size: 100%;
}
.slide4 {
  background-image: url('/static/img/b.jpg');
  color: #fff;
  background-size: 100%;
}
.slide5 {
  background-image: url('/static/img/c.jpg');
  color: #fff;
  background-size: 100%;
}
</style>




